<template>
	<div id="show-blogs" v-theme:column="'wide'">
		<h1>博客总览</h1>
		<input type="text" v-model="search" placeholder="搜索"/>
		<div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
			<router-link :to="'/my-blog/blog/'+blog.id">
				<h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
			</router-link>
			<article>
				{{blog.content | snippet}}
			</article>
		</div>
	</div>
</template>

<script>
	export default {
		name:"show-blogs",
		data(){
			return{
				blogs:[],
				search:""
			}
		},
		created(){
			this.$http.get("https://my-blog-4442e.firebaseio.com/posts.json")
				.then(function(data){
					return data.json();
				})
				.then(function(data){
					var blogsArray=[];
					for(let key in data){
						data[key].id=key;
						blogsArray.push(data[key]);
					}
					this.blogs=blogsArray;
					// console.log(this.blogs);
				})
		},
		computed:{
			filteredBlogs:function(){
				return this.blogs.filter((blog) =>{
					return blog.title.match(this.search);
				})
			}
		}
	}
</script>

<style scoped>
	#show-blogs{
		max-width: 800px;
		margin: 0 auto;
	}
	.single-blog{
		padding: 10px;
		margin: 10px 0;
		box-sizing: border-box;
		background: #eee;
	}
	a{
		text-decoration: none;
	}
	input[type="text"]{
		width: 100%;
		height: 25px;
	}
</style>



