<template>
	<div id="add-blog" v-theme="'wide'">
		<div v-if="!submmited">
			<h2>添加博客</h2>
			<form>
				<label>博客标题</label>
				<input type="text" v-model="blog.title" required/>
				<label>博客内容</label>
				<textarea v-model="blog.content"></textarea>
					<div id="checkboxes">
						<label>vue.js</label>
						<input type="checkbox" value="vue.js" v-model="blog.categories">
						<label>Node.js</label>
						<input type="checkbox" value="Node.js" v-model="blog.categories">
						<label>anglar.js</label>
						<input type="checkbox" value="anglar.js" v-model="blog.categories">
						<label>react.js</label>
						<input type="checkbox" value="react.js" v-model="blog.categories">
					</div>
					<label>作者：</label>
					<select v-model="blog.author">
						<option v-for="author in authors" :key="author">
							{{author}}
						</option>
					</select>
					<button @click.prevent="post">添加博客</button>
			</form>
		</div>
		<div v-if="submmited">
			<h3>你的博客发布成功</h3>
		</div>
		<hr/>
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题：{{blog.title}}</p>
			<p>博客内容：{{blog.content}}</p>
			<p>博客分类:</p>
			<ul v-for="category in blog.categories" :key="category">
				<li>{{category}}</li>
			</ul>
			<p>博客作者:{{blog.author}}</p>
		</div>
  </div>
</template>

<script>
export default {
	//http://jsonplaceholder.typicode.com/posts  post网址
  name: 'add-blog',
  data () {
    return {
      blog:{
				title:"",
				content:"",
				categories:[],
				author:""
			},
			authors:["hemiah","zhangsan","lisi"],
			submmited:false
    }
  },
	methods:{
		post:function(){
			this.$http.post("https://my-blog-4442e.firebaseio.com/posts.json",this.blog) 
			.then(function(data){
				this.submmited=true;
			});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	#add-blog *{
		box-sizing: border-box;
	}
	#add-blog{
		margin: 0 auto;
		max-width: 800px;
	}
	label{
		display: block;
		margin: 20px 0 10px;
	}
	input[type="text"],textarea,select{
		display: block;
		width:100%;
		height: 25px;
		padding:4px;
	}
	textarea{
		height: 60px;
	}
	#checkboxes label{
		display: inline-block;
		margin-top: 0;
	}
	#checkboxes input{
		display: inline-block;
		margin-right: 10px;
	}
	button{
		display: block;
		margin: 20px 0;
		background-color: crimson;
		color: #fff;
		border: 0;
		padding: 14px;
		cursor:pointer;
		border-radius: 4px;
		font-size: 18px;
	}
	#preview{
		padding: 10px 20px;
		border: 1px dotted #ccc;
		margin: 30px 0;
	}
</style>



