<template>
	<div id="single-blog" v-theme="'wide'">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者:{{blog.author}}</p>
		<p>分类：</p>
		<ul v-for="category in blog.categories" :key="category">
			<li>{{category}}</li>
		</ul>
		<button @click="removeBlog()">删除</button>
		<router-link :to="'/my-blog/edit/'+id">编辑</router-link>
	</div>
</template>

<script>
	export default {
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get("https://my-blog-4442e.firebaseio.com/posts/"+this.id+".json")
				.then(function(data){
					this.blog=data.body;
					// console.log(this.blog);
				})
		},
		methods:{
			removeBlog(){
				this.$http.delete("https://my-blog-4442e.firebaseio.com/posts/"+this.id+".json")
					.then(response=>{
						this.$router.push({path:"/"});
					})
			}
		}
	}
</script>

<style scoped>
#single-blog {
    padding: 10px;
    margin: 0 auto;
    box-sizing: border-box;
    background: #eee;
}
a{	
	float:left;
	margin-left: 14px;
	display: block;
	width: 26.663px;
	height: 18.4px;
	padding:1px 7px;
	text-align: center;
	color: #000;
	text-decoration: none;
	border: 1px outset #999;
	border-image: initial;
	font-size: 13.3333px;
}
</style>



